
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

body {
  color: #243B53;
  font-family: $bodyFont;
}


* {
  margin: 0;
  padding: 0;
}


.logo {
  display: block;
}

nav ul {
  padding: 12px 0;
  display: flex;
  justify-content: space-between;
}

nav,
main {
  width: 650px;
  max-width: 90%;
  margin: 48px auto;
}

nav ul {
  list-style: none;
  padding-left: 0;
}

h1, h2, h3 {
  color: #102A43;
  margin-top: 24px;
  margin-bottom: 12px;
}

p {
  color: #243B53;
  line-height: 24px;
}